@import url(https://fonts.googleapis.com/css?family=Mountains+of+Christmas:700);
@import url(https://fonts.googleapis.com/css?family=Mountains+of+Christmas);
body {
  margin: 0;
  font-family: 'Mountains of Christmas';
  text-align: center;
  background-color: #233050;
  background-image: url(/advent_calendar/static/media/stars.0bb218dc.jpg);
  background-repeat: repeat;
  margin: 0;
  text-align: -webkit-center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'Mountains of Christmas';
  font-size: 72px;
  color: #d7e4ab;
}

.card-list {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  width: 50vw;
  position: relative;
  padding-bottom: 100px;
  padding-top: 10vw;
}

.card-container {
  display: flex;
  flex-direction: column;
  background-color: #ffc847;

  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
  margin: 10px;
}

.card-container-no-efect {
  display: flex;
  flex-direction: column;
  background-color: #ffc847;
  opacity: 0.6;
  border-radius: 5px;
  padding: 5px;
  -moz-osx-font-smoothing: grayscale;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
  margin: 10px;
}

.card-container:hover {
  transform: scale(1.05);
}

h2 {
  color: #030329ea;
  font-size: 30px;
}

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

#circle {
  width: 100px;
  height: 100px;
  background-color: #ffc847;
  border-radius: 100px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}
#circle:after {
  content: '';
  display: block;
  border: 4px dashed white;
  width: 92px;
  height: 92px;
  border-radius: 92px;
  -webkit-animation: spin 30s linear infinite;
          animation: spin 30s linear infinite;
}
#circle:hover {
  background-image: radial-gradient(#ffed85, #ffc847 70%);
}
#gift {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  width: 50px;
  height: 80px;
}
#ribbon {
  position: relative;
  width: 30px;
  height: 30px;
  transform: rotate(45deg);
  margin: auto;
  margin-bottom: -12px;
  border-radius: 0 8px 0 8px;
}
#ribbon:before {
  content: '';
  height: 12px;
  width: 100%;
  background-color: #f04d4d;
  display: block;
  position: absolute;
  top: 9px;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.4);
  border-radius: inherit;
}
#ribbon:after {
  content: '';
  height: 100%;
  width: 12px;
  background-color: #f04d4d;
  display: block;
  margin: auto;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.4);
  border-radius: inherit;
}
#giftbox {
  position: relative;
  margin: auto;
  width: 50px;
  height: 50px;
  background-color: white;
  border-radius: 1px;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.4);
  overflow: hidden;
}
#giftbox:before {
  content: '';
  height: 24%;
  width: 100%;
  background-color: #f04d4d;
  display: block;
  position: absolute;
  top: 38%;
  box-shadow: inherit;
}
#giftbox:after {
  content: '';
  height: 100%;
  width: 24%;
  background-color: #f04d4d;
  display: block;
  margin: auto;
  box-shadow: inherit;
}

